// Library Imports
import React from "react";

// Relative Imports
import { Logo } from "./styles";
import WhiteTextLogo from "../../../assets/icons/shorelogo2.png";
import BlueTextLogo from "../../../assets/icons/shorelogo.png";
import BlueLogo from "../../../assets/icons/shorelogo1.png";
import WhiteLogo from "../../../assets/icons/shorelogo2.png";

const HavenLogo = props => {
  return (
    <Logo
      styling={props.styling}
      src={
        props.blue && props.symbol
          ? BlueLogo
          : props.white && props.symbol
          ? WhiteLogo
          : props.white & props.text
          ? WhiteTextLogo
          : BlueTextLogo
      }
    />
  );
};

export default HavenLogo;
